import { Injectable } from '@angular/core';
import { includes, get } from 'lodash';

import { IDefinitionAdapter } from '@helix/platform/shared/api';
import { RX_LEGACY_ICONS, IContainerViewComponentDefinition, IViewDefinition } from '@helix/platform/view/api';
import { ActionButtonSize, ActionButtonStyle, DeprecatedActionButtonStyle } from './action-button.types';

@Injectable()
export class ActionButtonAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition, IViewDefinition>
{
  private deprecatedButtonStyles = [DeprecatedActionButtonStyle.Link, DeprecatedActionButtonStyle.Clear];

  adaptDefinition(componentDefinition: IContainerViewComponentDefinition) {
    if (includes(this.deprecatedButtonStyles, componentDefinition.propertiesByName.style)) {
      componentDefinition.propertiesByName.style = ActionButtonStyle.Tertiary;
    }

    if (!componentDefinition.propertiesByName.size) {
      componentDefinition.propertiesByName.size = ActionButtonSize.Small;
    }

    if (componentDefinition.propertiesByName.icon) {
      componentDefinition.propertiesByName.icon = get(
        RX_LEGACY_ICONS,
        componentDefinition.propertiesByName.icon,
        componentDefinition.propertiesByName.icon
      );
    }
  }
}
